
#header {
  border-top: 6px solid #1C5F30;
}

#logo {
  font-size: 1.5em;
  line-height: 40px;
  color: #2c3e50;
  font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 500;
}

#language-switch {
  cursor: pointer;
  font-size: 13px;
  border-radius: 3px;
  padding: 3px 6px;
  color: #1C5F30;
  border: 1px solid #1C5F30;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

#language-switch:hover {
  background-color: #1C5F30;
  color: #fff
}

#logo img {
  vertical-align: middle;
  margin-right: 6px;
  height: 40px;
}

#nav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  right: 60px;
  top: 25px;
  height: 40px;
  line-height: 40px;
  font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;

}

#nav li {
  display: inline-block;
  position: relative;
  margin: 0 1em;
}

#nav a {
  padding-bottom: 5px;
  border-bottom: 3px solid #fff;
  -webkit-transition: 0.2s;
  transition: 0.2s
}

#nav li a:hover {
  border-bottom: 3px solid #1C5F30;
}

#nav .current{
  border-bottom: 3px solid #1C5F30;
}




