
#footer {
  background-color: #424242;
  font-family: 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
}
#footer-top{
  display: inline-block;
}
#footer-top ul {
  margin-bottom:0;
  margin-top: 45px
}
#footer-top ul li {
  font-size: 13px;
  line-height: 20px;
  color: #E0E0E0;
}

#footer-bottom p {
  text-align: center;
  color: #E0E0E0;
  padding-bottom: 10px;
  font-size: 13px;
}

.footer-section {
  float: left;
  clear: right;
}

.footer-section ul {
  list-style: none;
  color: #fff
}



